<style src="@/assets/css/main.css"></style>
<template>
  <div>

    <b-navbar toggleable="md" type="dark" variant="success">

      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">星云TodoList</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item @click="push">
            {{loginTitle}}
          </b-nav-item>
          <b-nav-item href="https://github.com/ChengOrangeJu/WebExtensionWallet">星云钱包插件</b-nav-item>
          <b-nav-item href="https://incentive.nebulas.io/">Nebulas活动</b-nav-item>
          <b-nav-item href="mailto:forestyiqing@gmail.com">联系我们</b-nav-item>


          <!--<b-nav-item-dropdown text="Lang" right>-->
            <!--<b-dropdown-item href="#">EN</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">ES</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">RU</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">FA</b-dropdown-item>-->
          <!--</b-nav-item-dropdown>-->

          <!--<b-nav-item-dropdown right>-->
            <!--&lt;!&ndash; Using button-content slot &ndash;&gt;-->
            <!--<template slot="button-content">-->
              <!--<em>User</em>-->
            <!--</template>-->
            <!--<b-dropdown-item href="#">Profile</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">Signout</b-dropdown-item>-->
          <!--</b-nav-item-dropdown>-->
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
    <div class="container">
      <div class="row">
        <div class="col col-12">
          <!-- Content will be placed here -->
          <slot></slot>
        </div>
      </div>
    </div>

    <div class="footer text-center bg-success text-white">
      <span>POWERED BY <img src="@/assets/imgs/nebulasx60.png" alt="" height="30px">   Copyright @ 2018</span>

    </div>
  </div>
</template>

<script>
  /* ============
   * Default Layout
   * ============
   *
   * Used for the home and account pages.
   *
   * Layouts are used to store a lot of shared code.
   * This way the app stays clean.
   */

  export default {
    /**
     * The name of the layout.
     */
    name: 'default-layout',

    /**
     * The data that can be used by the page.
     *
     * @returns {Object} The view-model data.
     */
    data() {
      return {
        menuCollapsed: false,
      };
    },

    /**
     * The methods that the layout can use.
     */
    methods: {
      /**
       * Will log the user out.
       */
      logout() {
        this.$store.dispatch('auth/logout');
      },

      /**
       * Will toggle the menu.
       */
      toggleMenu() {
        this.menuCollapsed = !this.menuCollapsed;
      },

      push() {
        console.log('push');
        this.$router.push({name:'login'})
      }
    },
    computed: {
      loginTitle() {
        return this.$userlib.isLogin()?'登出':'登录/注册'
      }
    },
  };
</script>



