<template>
  <v-layout>
    <section class="todoapp">
      <header class="header" style="padding-top: 20px">
        <!--<h1>todos</h1>-->
        <h2 class="text-center">{{user.username}}的私人任务列表</h2>
        <input class="new-todo"
               autofocus autocomplete="off"
               placeholder="添加新的任务?"
               v-model="newTodo"
               @keyup.enter="addTodo">
      </header>
      <section class="main" v-show="todos.length" v-cloak>
        <!--<input class="toggle-all" type="checkbox" v-model="allDone">-->
        <ul class="todo-list">
          <li v-for="todo in filteredTodos"
              class="todo"
              :key="todo.id"
              :class="{ completed: todo.completed, editing: todo == editedTodo }">
            <div class="view">
              <input class="toggle" type="checkbox" v-model="todo.completed" @click="toggleComplete(todo)">
              <label @dblclick="editTodo(todo)">{{ todo.text }}</label>
              <!--<button class="destroy" @click="removeTodo(todo)"></button>-->
            </div>
            <!--<input class="edit" type="text"-->
                   <!--v-model="todo.title"-->
                   <!--v-todo-focus="todo == editedTodo"-->
                   <!--@blur="doneEdit(todo)"-->
                   <!--@keyup.enter="doneEdit(todo)"-->
                   <!--@keyup.esc="cancelEdit(todo)">-->
          </li>
        </ul>
      </section>
      <footer class="footer" v-show="todos.length" v-cloak>
    <span class="todo-count">
      <strong>{{ remaining }}</strong> {{ remaining | pluralize }} left
    </span>
        <ul class="filters">
          <li><a @click="select('all')" :class="{ selected: visibility == 'all' }">All</a></li>
          <li><a @click="select('active')" :class="{ selected: visibility == 'active' }">Active</a></li>
          <li><a @click="select('completed')" :class="{ selected: visibility == 'completed' }">Completed</a></li>
        </ul>
        <!--<button class="clear-completed" @click="removeCompleted" v-show="todos.length > remaining">-->
          <!--Clear completed-->
        <!--</button>-->
      </footer>
    </section>

    <!--<footer class="info">-->
      <!--<p>Double-click to edit a todo</p>-->
      <!--<p>Written by <a href="http://evanyou.me">Evan You</a></p>-->
      <!--<p>Part of <a href="http://todomvc.com">TodoMVC</a></p>-->

    <!--</footer>-->

    <b-modal ref="loginModal" centered title="您还没有登录！" @ok="pushLogin">
      <p class="my-4">您需要登录之后才能创建私人的区块链任务!</p>
    </b-modal>
  </v-layout>
</template>
<script>

  import NebPay from 'nebpay.js';
  import nebulas from 'nebulas';
  import VLayout from '@/layouts/NavLayout';
  import VCard from '@/components/Card'

  var STORAGE_KEY = 'todos-vuejs-2.0'
  var todoStorage = {
    fetch: function () {
      var todos = JSON.parse(localStorage.getItem(STORAGE_KEY) || '[]')
      todos.forEach(function (todo, index) {
        todo.id = index
      })
      todoStorage.uid = todos.length
      return todos
    },
    save: function (todos) {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(todos))
    }
  }

  // visibility filters
  var filters = {
    all: function (todos) {
      return todos
    },
    active: function (todos) {
      return todos.filter(function (todo) {
        return !todo.completed
      })
    },
    completed: function (todos) {
      return todos.filter(function (todo) {
        return todo.completed
      })
    }
  }


  export default {
    components: {
      VLayout,
      VCard
    },
    /**
     * Chart data used to render stats, charts. Should be replaced with server data
     */
    data() {
      return {
        dappAddress: '',
//        list: [],
//        todos: todoStorage.fetch(),
        todos: [],
        newTodo: '',
        editedTodo: null,
        visibility: 'all',
        user: null
      };
    },
    created() {
      //从配置中读取智能合约DAPP地址
      this.dappAddress = this.$neblib.dappAddress
      console.log('created dapp address', this.dappAddress);

      // 坚持星云钱包插件
      if (typeof (webExtensionWallet) === "undefined") {
        alert("请先安装星云钱包插件.");
      }
      this.user = this.$userlib.user()

      this.queryTodos()
    },


    // watch todos change for localStorage persistence
    watch: {
      todos: {
        handler: function (todos) {
          todoStorage.save(todos)
        },
        deep: true
      }
    },

    // computed properties
    // http://vuejs.org/guide/computed.html
    computed: {
      filteredTodos: function () {
        return filters[this.visibility](this.todos).reverse()
//        return filters[this.visibility](this.todos)
      },
      remaining: function () {
        return filters.active(this.todos).length
      },
      allDone: {
        get: function () {
          return this.remaining === 0
        },
        set: function (value) {
          this.todos.forEach(function (todo) {
            todo.completed = value
          })
        }
      }
    },

    filters: {
      pluralize: function (n) {
        return n === 1 ? 'item' : 'items'
      },
      reverse: function (list) {
        return list.reverse()
      }
    },

    methods: {
      // 查询所有的
      pushLogin() {
//        console.log('push login');
        this.$router.push({name: 'login'})
      },
      select(visibility) {
        console.log('selected ', visibility);
        this.visibility = visibility
      },
      toggleComplete(todo){
        console.log('toggle complete', JSON.stringify(todo));
        let completed = todo.completed?"0":"1"
        console.log('completed ', completed);
        this.$neblib.callQueryPayInfo('update', [todo.id, todo.text, completed], 0, (err) => {
          if (err){
            return alert(err)
          }
          this.queryTodos()
        })

      },
      queryTodos(){
        this.$neblib.query('get', this.user.username, (err, result) => {
          if (err) {
            return alert(err)
          }
//          console.log('====result ', typeof result);
//          console.log(result);

          let list = JSON.parse(result)
          list.list.forEach(function (todo) {
            todo.completed = parseInt(todo.completed)
          })

          console.log('todos ', list.list);
          this.todos = list.list
        })
      },

      pushNew() {
        console.log('push new');
        this.$router.push({name:'new'})
      },
      addTodo: function () {

        //TODO: 判断用户
        if(!this.user || true){
          return this.$refs.loginModal.show()
        }

        var value = this.newTodo && this.newTodo.trim()
        if (!value) {
          return
        }

        console.log('add ', value);

        this.$neblib.callQueryPayInfo('add', [this.user.username, value], 0, (err) => {
          if (err){
            return alert(err)
          }

          this.queryTodos()
          this.newTodo = ''
        })
      },
      removeTodo: function (todo) {
        this.todos.splice(this.todos.indexOf(todo), 1)
      },

      editTodo: function (todo) {
        console.log('edit todo', todo);
        this.beforeEditCache = todo.title
        this.editedTodo = todo
      },

      doneEdit: function (todo) {
        if (!this.editedTodo) {
          return
        }
        this.editedTodo = null
        todo.title = todo.title.trim()
        if (!todo.title) {
          this.removeTodo(todo)
        }
      },

      cancelEdit: function (todo) {
        this.editedTodo = null
        todo.title = this.beforeEditCache
      },

      removeCompleted: function () {
        this.todos = filters.active(this.todos)
      }
    },
    directives: {
      'todo-focus': function (el, binding) {
        if (binding.value) {
          el.focus()
        }
      }
    }
  };

</script>
<style>
  [v-cloak] { display: none; }
</style>



